body {
  margin: 0px;
}

.login {
  height: 100vh;
  background-color: rgb(255, 199, 125);
  display: flex;
  align-items: center;
  justify-content: center;

  .card {
    border-radius: 10px;
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
      rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
    width: 50%;
    overflow: hidden;
    display: flex;
    background-color: rgb(255, 255, 255);
    border-radius: 10px;
    min-height: 600px;

    .left {
      flex: 1;

      background: linear-gradient(rgba(56, 42, 24, 0.795), (rgba(0, 0, 0, 0.5))),
        url("https://plus.unsplash.com/premium_photo-1670884126758-cae320f7be47?ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D&auto=format&fit=crop&w=1887&q=80")
          center;
      background-size: cover;
      padding: 50px;
      display: flex;
      height: 50%;
      flex-direction: column;
      gap: 30px;
      color: white;

      h1 {
        font-family: "Anton", sans-serif;

        font-size: 100px;
        line-height: 100px;
      }
      p {
        font-family: "Anton", sans-serif;
      }
      span {
        font-family: "Anton", sans-serif;
        font-size: 14px;
      }
      button {
        border-radius: 5px;
        width: 50%;
        padding: 10px;
        border: none;
        background-color: rgb(250, 248, 238);
        color: black;
        font-weight: bold;
        cursor: pointer;
        box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
          rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
      }
    }
    .right {
      flex: 1;
      flex: 1;
      padding: 50px;
      display: flex;
      flex-direction: column;
      gap: 50px;
      justify-content: center;
      h1 {
        font-family: "Anton", sans-serif;
        color: #474747;
      }
      form {
        display: flex;
        flex-direction: column;
        gap: 30px;
      }
      input {
        border: none;
        border-bottom: 1px solid lightgray;
        padding: 20px 10px;
        outline: none;
      }
      button {
        border-radius: 5px;
        width: 50%;
        padding: 10px;
        border: none;
        background-color: rgb(255, 199, 125);
        box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
          rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
        color: #474747;

        font-weight: bold;
        cursor: pointer;
      }
    }
  }
}

