@import "../../style.scss";

.navbar {
  @include themify($theme) {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 20px;
    height: 50px;
    position: sticky;
    border-bottom: 1px solid themed("border");
    top: 0;
    background-color: themed("bg");
    color: themed("textColor");
    z-index: 999;
    .left {
      display: flex;
      align-items: center;
      gap: 30px;
      span {
        font-family: "Anton", sans-serif;
        font-weight: bold;
        font-size: 25px;
        color: themed("logo");
      }

      // img {
      //   width: 50px;
      //   height: 50px;
      //   border-radius: 50%;
      //   object-fit: cover;
      //   border-style: solid;
      //   border-width: 1px;
      //   color: black;
      // }
      .search {
        gap: 10px;
        border: 1px solid themed("border");
        display: flex;
        align-items: center;
        border-radius: 5px;
        padding: 5px;
        input {
          border: none;
          outline: none;
          width: 800px;
          background-color: transparent;
          color: themed("textColor");
          @include mobile {
            display: none;
          }

          @include tablet {
            width: 200px;
          }
        }
      }
    }
    .right {
      display: flex;
      align-items: center;
      gap: 20px;
      @include mobile {
        display: none;
      }
      .user {
        display: flex;
        align-items: center;
        gap: 10px;
        font-weight: 500;

        @include tablet {
          display: none;
        }
        img {
          width: 30px;
          height: 30px;
          border-radius: 50%;
          object-fit: cover;
          box-shadow: rgba(0, 0, 0, 0.3) 0px 1px 2px 0px,
            rgba(60, 64, 67, 0.15) 0px 2px 2px 1px;
        }
        span {
          font-family: "Anton", sans-serif;
          font-weight: bold;
        }
      }
    }
  }
}
